import Notification from '../../components/Notification.vue'
export default {
  layout: 'empty',

  data() {
    return {
      email: '',
      password: '',
      errorMessage: '',
      isLoading: false,
    }
  },
  components: {
    Notification,
  },
  methods: {
    // async loginAuto() {
    //   console.log('auto login')
    //   const payload = {
    //     mem_id: this.email,
    //     mem_pwd: this.password,
    //   }
    //   try {
    //     await this.$auth.loginWith('local', {
    //       data: payload,
    //     })
    //     this.$router.push('/')
    //   } catch (e) {
    //     this.$router.push('/login')
    //   }
    // },
   async login() {
      this.isLoading = true
      this.errorMessage = ''
      console.log('Hello login')
      const payload = {
        mem_id: this.email,
        mem_pwd: this.password,
      }
      console.log("This .isloaing: "+this.isLoading);
      await this.$axios
        .post('mem_auth', payload)
        .then((res) => {
          console.log('RES: ' + res.data)
          const resData = res.data
          console.log('Status: ' + res.status)
          if (res.status !== 200) {
            this.isLoading = false
            this.errorMessage = 'ບໍ່ສາມາດ ລັອກອິນ ກະລຸນາລອງໃຫມ່ ພາຍຫລັງ'
            return
          }

          console.log('Token: ' + resData.accessToken)
          if (resData.accessToken) {
            console.log('Get token')
            const payload = {
              name: resData.userName,
              id: resData.userId,
              phone: resData.userPhone,
              token: resData.accessToken,
            }
            this.$store.dispatch('login', payload)
            console.log(this.$store.getters.oggedInUser)
            this.$router.push('/')
          } else {
            console.log('No token')
            this.errorMessage = 'ໄອດີ ຫລື ລະຫັດຜ່ານ ບໍ່ຖືກຕ້ອງ'
          }
        })
        .catch((er) => {
          console.log('ERROR: ' + er)
          this.errorMessage = er.data
        })
      this.isLoading = false
      console.log('payload: ' + payload)
    },
  },
}
